nav .nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 40px;
}

nav .nav .nav_phone_burger {
  display: flex;
  gap: 12px;
  padding: 11px;
}

nav .nav .nav_logo {
  display: flex;
  gap: 15px;
  justify-content: center;
  align-items: center;
}

nav .nav .burger__menu {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  align-content: space-between;
  width: 30px;
  height: 20px;
  padding: 12px;
}

nav .nav .burger__menu .burger {
  display: block;
  background: #000000;
  width: 100%;
  height: 2px;
  border-radius: 4px;
  transition: 0.3s;
}

nav .nav .burger__menu .burger :hover {
  color: rgba(187, 68, 68, 0.808);
}

nav .nav .burger__menu.active .burger:nth-child(1) {
  transform: rotate(-45deg) translate(-11px, 1px);
}

nav .nav .burger__menu.active .burger:nth-child(2) {
  display: none;
}

nav .nav .burger__menu.active .burger:nth-child(3) {
  transform: rotate(45deg) translate(-11px, -2px);
}

nav .nav .nav_menu {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 45px;
}

nav .nav .nav_menu .nav__menu__item a {
  font-family: "Raleway";
